<template>
  <div id="main"></div>
</template>
<script>
export default {
  data() {
    return {};
  },
  methods: {
    fillChart1(content) {
      console.log(content);
      let xAxisArry = [];
      let yAxisArry = [];
      content.forEach((item) => {
        xAxisArry.push(item.DateStr);
        yAxisArry.push(item.Power);
      });
      let chart_projectLoad = this.$echarts.init(
        document.getElementById("main")
      );
      chart_projectLoad.clear();
      var option = {
        xAxis: {
          type: "category",
          boundaryGap: true,
          data: xAxisArry,
          axisLabel: {
            show: true,
            textStyle: {
              color: "#ffffff",
            },
          },
          axisTick: {
            show: false,
          },
        },
        yAxis: {
          type: "value",
          axisLabel: {
            show: true,
            textStyle: {
              color: "#ffffff",
            },
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: ["rgba(255, 255, 255, 0.5)"],
              type: "dashed",
            },
          },
        },
        series: [
          {
            data: yAxisArry,
            type: "line",
            lineStyle: {
              color: "#0044FF",
            },
            areaStyle: {
              color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: "rgba(4, 0, 255, 0.52)",
                },
                {
                  offset: 1,
                  color: "rgba(0, 132, 255, 0.51)",
                },
              ]),
            },
            smooth: true,
          },
        ],
      };

      chart_projectLoad.setOption(option);
    },
  },
};
</script>
<style scoped>
#main {
  width: 100%;
  height: 100%;
}
</style>